import { Button, Card, CardContent, CircularProgress, Grid, TextField, Typography } from "@mui/material";
import {useState} from 'react'
import { useNavigate } from "react-router-dom";

export default function RegisterForm(){
    const [data, setData] = useState({
        cedula: "",
        nombre: "",
        apellido: "",
        correo: "",
        telefono: "",
        usuario: "",
        contraseña: ""
    })
    const [loading, setLoading] = useState(false)
    const navigate = useNavigate()
    const handleChange = e => {
        setData({...data, [e.target.name]: e.target.value});
    }
    const handleSubmit = async(e) => {
        e.preventDefault();
        setLoading(true)
        const res = await fetch('http://api.turisteando.xyz/registro',{
            method:'POST',
            body: JSON.stringify(data),
            headers: {'Content-Type': 'application/json'}
        })
        const response = await res.json()
        console.log(response)
        setLoading(false)
        navigate("/")
    }
	return(
		<Grid container direction='column' alignItems='center' justifyContent='center'>
            <Grid item xs={3}>
                <Card sx={{mt:5}} style={{
                    backgroundColor: "#1e272e",
                    padding: "1rem"
                }}>
                    <Typography variant='5' textAlign="center" color="white"> Registro </Typography>
                    <CardContent>
                        <form onSubmit={handleSubmit}>
                            <TextField 
                                variant='filled' 
                                label='Cedula'
                                sx={{
                                    display: 'block',
                                    margin: '.5rem 0'
                                }}
                                disabled={loading}
                                name = 'cedula'
                                onChange={handleChange}
                                inputProps={{style: {color: "white"}}}
                                InputLabelProps={{style: {color: "white"}}}
                            />
                            <TextField 
                                variant='filled' 
                                label='Nombre'
                                sx={{
                                    display: 'block',
                                    margin: '.5rem 0'
                                }}
                                disabled={loading}
                                name = 'nombre'
                                onChange={handleChange}
                                inputProps={{style: {color: "white"}}}
                                InputLabelProps={{style: {color: "white"}}}
                            />
                            <TextField 
                                variant='filled' 
                                label='Apellido'
                                sx={{
                                    display: 'block',
                                    margin: '.5rem 0'
                                }}
                                disabled={loading}
                                name = 'apellido'
                                onChange={handleChange}
                                inputProps={{style: {color: "white"}}}
                                InputLabelProps={{style: {color: "white"}}}
                            />
                            <TextField 
                                variant='filled' 
                                label='Correo'
                                sx={{
                                    display: 'block',
                                    margin: '.5rem 0'
                                }}
                                disabled={loading}
                                type="email"
                                name = 'correo'
                                onChange={handleChange}
                                inputProps={{style: {color: "white"}}}
                                InputLabelProps={{style: {color: "white"}}}
                            />
                            <TextField 
                            variant='filled' 
                            label='Telefono'
                            sx={{
                                display: 'block',
                                margin: '.5rem 0'
                            }}
                            disabled={loading}
                            name = 'telefono'
                            onChange={handleChange}
                            inputProps={{style: {color: "white"}}}
                            InputLabelProps={{style: {color: "white"}}}
                        />
                        <TextField 
                                variant='filled' 
                                label='Usuario'
                                sx={{
                                    display: 'block',
                                    margin: '.5rem 0'
                                }}
                                disabled={loading}
                                name = 'usuario'
                                onChange={handleChange}
                                inputProps={{style: {color: "white"}}}
                                InputLabelProps={{style: {color: "white"}}}
                            />
                            <TextField 
                                variant='filled' 
                                label='Contraseña'
                                sx={{
                                    display: 'block',
                                    margin: '.5rem 0'
                                }}
                                disabled={loading}
                                type="password"
                                name = 'contraseña'
                                onChange={handleChange}
                                inputProps={{style: {color: "white"}}}
                                InputLabelProps={{style: {color: "white"}}}
                            />
                            <Button variant='contained' 
                                    color="primary" 
                                    type="submit" 
                                    disabled={!data.usuario || 
                                            !data.contraseña ||
                                            !data.nombre ||
                                            !data.apellido ||
                                            !data.cedula ||
                                            !data.correo ||
                                            !data.telefono ||
                                            loading} >
                                {loading ? <CircularProgress
                                    color="inherit"
                                    size={24}
                                />: "Registrarse"}
                            </Button>
                        </form>
                    </CardContent>
                </Card>
            </Grid>
        </Grid>
	)
}