import { Button, Card, CardContent, CircularProgress, Grid, TextField, Typography } from "@mui/material";
import {useState} from 'react'
import { useNavigate } from "react-router-dom";
import { credentiales } from "../utils/credentials";
export default function LoginForm(){
	const [data, setData] = useState({
        usuario: "",
        contraseña: ""
    })
    const [loading, setLoading] = useState(false)
    const navigate = useNavigate()
    const handleChange = e => {
        setData({...data, [e.target.name]: e.target.value});
    }
    const handleSubmit = async(e) => {
        e.preventDefault();
        setLoading(true)
        const res = await fetch('http://api.turisteando.xyz/login',{
            method:'POST',
            body: JSON.stringify(data),
            headers: {'Content-Type': 'application/json'}
        })
        const response = await res.json()
        console.log(response)
		setLoading(false)
		if (response.succes){
			credentiales.usuario_id = response.usuario.id
			credentiales.nombre = response.usuario.nombre 
			credentiales.apellido = response.usuario.apellido
			credentiales.puntos = response.usuario.puntos 
			credentiales.correo = response.usuario.correo
			credentiales.isLogin = true
			console.log(credentiales)
        	navigate("/jugar")
		}else{
			alert("Hubo un error al tratar de loguear")
		}
        
    }
	return(
		<Grid container direction='column' alignItems='center' justifyContent='center'>
            <Grid item xs={3}>
                <Card sx={{mt:5}} style={{
                    backgroundColor: "#1e272e",
                    padding: "1rem"
                }}>
                    <Typography variant='5' textAlign="center" color="white"> LogIn </Typography>
                    <CardContent>
                        <form onSubmit={handleSubmit}>
                        <TextField 
                                variant='filled' 
                                label='Usuario'
                                sx={{
                                    display: 'block',
                                    margin: '.5rem 0'
                                }}
                                disabled={loading}
                                name = 'usuario'
                                onChange={handleChange}
                                inputProps={{style: {color: "white"}}}
                                InputLabelProps={{style: {color: "white"}}}
                            />
                            <TextField 
                                variant='filled' 
                                label='Contraseña'
                                sx={{
                                    display: 'block',
                                    margin: '.5rem 0'
                                }}
                                disabled={loading}
                                type="password"
                                name = 'contraseña'
                                onChange={handleChange}
                                inputProps={{style: {color: "white"}}}
                                InputLabelProps={{style: {color: "white"}}}
                            />
                            <Button variant='contained' 
                                    color="primary" 
                                    type="submit" 
                                    disabled={!data.usuario || 
                                            !data.contraseña ||
                                            loading} >
                                {loading ? <CircularProgress
                                    color="inherit"
                                    size={24}
                                />: "Ingresar"}
                            </Button>
                        </form>
                    </CardContent>
                </Card>
            </Grid>
        </Grid>
	)
}