import React, { useState, useRef, useEffect } from "react";
import { useInterval } from "./useInterval";
import {
  CANVAS_SIZE,
  SNAKE_START,
  APPLE_START,
  SCALE,
  SPEED,
  DIRECTIONS
} from "../utils/constants";
import { Button } from "@mui/material";
import { credentiales } from "../utils/credentials";
import { useNavigate } from "react-router-dom";

const GameView = () => {
  const navigate = useNavigate()
  const canvasRef = useRef();
  const [snake, setSnake] = useState(SNAKE_START);
  const [apple, setApple] = useState(APPLE_START);
  const [dir, setDir] = useState([0, -1]);
  const [speed, setSpeed] = useState(null);
  const [gameOver, setGameOver] = useState(false);
  const [score, setScore] = useState(0);
  
  useInterval(() => gameLoop(), speed);

  const endGame = async () => {
	const puntos = snake.length -2
	const data = {
		usuario_id: credentiales.usuario_id,
		puntos: puntos
	}
	const res = await fetch('http://api.turisteando.xyz/game-over',{
		method:'POST',
		body: JSON.stringify(data),
		headers: {'Content-Type': 'application/json'}
	})
	const response = await res.json()
	console.log(response)
    setSpeed(null);
    setGameOver(true);
  };

  const moveSnake = ({ keyCode }) =>
    keyCode >= 37 && keyCode <= 40 && setDir(DIRECTIONS[keyCode]);

  const createApple = () =>
	apple.map((_a, i) => Math.floor(Math.random() * (CANVAS_SIZE[i] / SCALE)));
	
  
    

  const checkCollision = (piece, snk = snake) => {
    if (
      piece[0] * SCALE >= CANVAS_SIZE[0] ||
      piece[0] < 0 ||
      piece[1] * SCALE >= CANVAS_SIZE[1] ||
      piece[1] < 0
    )
      return true;

    for (const segment of snk) {
      if (piece[0] === segment[0] && piece[1] === segment[1]){
		return true;
	  } 
    }
    return false;
  };

  const checkAppleCollision = newSnake => {
    if (newSnake[0][0] === apple[0] && newSnake[0][1] === apple[1]) {
	  
      let newApple = createApple();
      while (checkCollision(newApple, newSnake)) {
        newApple = createApple();
      }
      setApple(newApple);
      return true;
    }
    return false;
  };

  const gameLoop = () => {
    const snakeCopy = JSON.parse(JSON.stringify(snake));
    const newSnakeHead = [snakeCopy[0][0] + dir[0], snakeCopy[0][1] + dir[1]];
    snakeCopy.unshift(newSnakeHead);
    if (checkCollision(newSnakeHead)) endGame();
    if (!checkAppleCollision(snakeCopy)) snakeCopy.pop();
    setSnake(snakeCopy);
  };

  const startGame = () => {
    setSnake(SNAKE_START);
    setApple(APPLE_START);
    setDir([0, -1]);
    setSpeed(SPEED);
    setGameOver(false);
  };

  useEffect(() => {
    const context = canvasRef.current.getContext("2d");
    context.setTransform(SCALE, 0, 0, SCALE, 0, 0);
    context.clearRect(0, 0, window.innerWidth, window.innerHeight);
    context.fillStyle = "pink";
    snake.forEach(([x, y]) => context.fillRect(x, y, 1, 1));
    context.fillStyle = "lightblue";
    context.fillRect(apple[0], apple[1], 1, 1);
  }, [snake, apple, gameOver]);

  return (
	  <>
	  {
	  	credentiales.isLogin ? 
		  <div role="button" tabIndex="0" onKeyDown={e => moveSnake(e)} style={{
			display: 'flex',
			justifyContent: "center",
			flexDirection: "column",
			justifyItems: "center",
			textAlign: "center"
		  }}>
		  <div>
		  {gameOver? <div><h3 style={{color: 'white' }}>GAME OVER!</h3>
		  </div>: 
		  <div>
			  {/* <h3 style={{color: 'white' }}>Score: {snake.lenght}</h3> */}
			  <h3></h3>
		  </div>}
		  <Button variant="contained" color='warning' onClick={startGame} style={{marginBottom: '0.75rem'}}>Empezar</Button>
		  </div>
		  <div>
		  <canvas
        style={{ border: "5px solid black" }}
        ref={canvasRef}
        width={`${CANVAS_SIZE[0]}px`}
        height={`${CANVAS_SIZE[1]}px`}
      />
		  </div>
      
    </div> :
	<div role="button" tabIndex="0" style={{
		display: 'flex',
		justifyContent: "center",
		flexDirection: "column",
		justifyItems: "center",
		textAlign: "center"
	  }}>
	  <div>
	  <h3></h3>
	  <Button variant="contained" color='warning' onClick={() => navigate("/")} style={{marginBottom: '0.75rem'}}>Login</Button>
	  </div>
	  <div>
	  <canvas
	style={{ border: "5px solid black" }}
	ref={canvasRef}
	width={`${CANVAS_SIZE[0]}px`}
	height={`${CANVAS_SIZE[1]}px`}
  />
	  </div>
  
</div>  
	  
	  }
	  
	  </>
    
	
  );
};

export default GameView;