import { Button, Card, CardContent, Typography } from "@mui/material"
import { useEffect, useState } from "react"
import { credentiales } from "../utils/credentials"

export default function ProductLists(){
	const handleCanje = async (product_id, total_puntos) => {
		const request = {
			"producto_id": product_id,
			"total_puntos": total_puntos,
			"cantidad_total": 1,
			"usuario_id": credentiales.usuario_id
		}
		console.log(request)
		const response = await fetch('http://api.turisteando.xyz/crear-canje', {
			method: 'POST',
			body: JSON.stringify(request),
			headers:  {'Content-Type': 'application/json'}
		})
		const res = await response.json()
		console.log(res)
		alert(`Tu codigo de canje es ${res.token} pronto nos pondremos en contacto contigo`)
	}
	const [products, setProducts] = useState([])
	const loadProducts = async () => {
		const response = await fetch('http://api.turisteando.xyz/productos')
		const data = await response.json()
		console.log(data.premios)
		setProducts(data.premios)
	}

	useEffect(() => {
		loadProducts()
	}, [])
	return(
		<>	
			<div style={{
				display: 'flex',
				justifyContent: "center",
				flexDirection: "column",
				justifyItems: "center",
				textAlign: "center"

			}}>
				<div>
					<h1 style={{color: 'white' }}>Lista de Productos</h1>
				</div>
				<div>
					<h4 style={{color: 'white' }}>Hola, {credentiales.nombre} tienes {credentiales.puntos} puntos para canjear</h4>
				</div>
			
			</div>
			
			{
				products.map(product => (
					<Card style={{
						marginBottom: ".7rem",
						backgroundColor: '#1e272e'
					}}>
						<CardContent style={{
							display: "flex",
							justifyContent: 'space-between'
						}}>
							<div style={{
								color: 'white'
							}}>
								<Typography>Nombre: {product.nombre}</Typography>
								<Typography>Puntos: {product.puntos}</Typography>
								<Typography>Stock: {product.stock}</Typography>
							</div>
							<div style={{
								
							}}>
							<Button variant="contained" color='warning' onClick={()=> handleCanje(product.id, product.puntos)}>Canjear</Button>
							</div>
							
						</CardContent>
					</Card>
				))
			}
		</>
	)
}