import {AppBar, Box, Button, Container, Toolbar, Typography} from '@mui/material'
import { Link, useNavigate } from 'react-router-dom'
import { credentiales } from '../utils/credentials';
export default function NavBar(){
    const navigate = useNavigate();
    const logout = () =>{
        credentiales.isLogin = false
        navigate("/")
    }
	return(
        <>
        <Box sx={{flexGrow:1}}>
            <AppBar position='static' color='transparent'>
                <Container>
                    <Toolbar>
                        <Typography variant= "h6" sx={{flexGrow:1}}>
                            <Link style={{textDecoration: "none", color: "#eee"}} to='/'>
                                Turisteando
                            </Link>
                        </Typography>
                        {
                            (!credentiales.isLogin) ? 
                            <div>
                                <Button variant = "contained" onClick={() => navigate("/")}>Login</Button>
                                <Button variant = "contained" onClick={() => navigate("/registro")} style={{ marginLeft: '0.5rem'}}>Registro</Button>
                            </div>
                             : 
                            <div>
                                <Button variant = "contained" onClick={() => navigate("/jugar")}>Jugar</Button>
                                <Button variant = "contained" onClick={() => navigate("/productos")} style={{ marginLeft: '0.5rem'}}>Productos</Button>
                                <Button variant = "contained" onClick={() => logout()} style={{ marginLeft: '0.5rem'}}>Salir</Button>
                            </div>
                            
                        }
                        
                            
                    </Toolbar>
                </Container>
            </AppBar>
        </Box>
        </>
		
	)
}